import { numberToMoney, dateToString } from 'utils/formatUtil';
import { Checkbox } from 'antd';
import React from 'react';

export const payableColumns = [
  {
    title: 'STT',
    dataIndex: 'index',
    key: 'index',
    align: 'center',
  },
  {
    title: 'Học kỳ',
    dataIndex: 'semester',
    key: 'semester',
    align: 'center',
  },
  {
    title: 'Năm học',
    dataIndex: 'yearEdu',
    key: 'yearEdu',
    align: 'center',
  },
  {
    title: 'Khoản thu',
    dataIndex: 'receivable',
    key: 'receivable',
  },
  {
    title: 'Số tiền phải nộp',
    dataIndex: 'amount',
    key: 'amount',
    align: 'right',
    render: text => numberToMoney(text),
  },
];

export const receiptColumns = [
  {
    title: 'STT',
    dataIndex: 'index',
    key: 'index',
    align: 'center',
  },
  {
    title: 'Học kỳ',
    dataIndex: 'semester',
    key: 'semester',
    align: 'center',
  },
  {
    title: 'Năm học',
    dataIndex: 'yearEdu',
    key: 'yearEdu',
    align: 'center',
  },
  {
    title: 'Ký hiệu',
    dataIndex: 'invoiceCode',
    key: 'invoiceCode',
    align: 'center',
  },
  {
    title: 'Số phiếu',
    dataIndex: 'invoiceNo',
    key: 'invoiceNo',
    align: 'center',
  },
  {
    title: 'Ngày tháng',
    dataIndex: 'paymentDate',
    key: 'paymentDate',
    align: 'center',
    render: text => dateToString(text),
  },
  {
    title: 'Người thu/chi',
    dataIndex: 'casher',
    key: 'casher',
    align: 'center',
  },
  {
    title: 'Khoản thu/chi',
    dataIndex: 'paymentContent',
    key: 'paymentContent',
    align: 'left',
  },
  {
    title: 'Số tiền',
    dataIndex: 'amount',
    key: 'amount',
    align: 'right',
    render: text => numberToMoney(text),
  },
  {
    title: 'Thu',
    dataIndex: 'isPayment',
    key: 'isPayment',
    align: 'center',
    render: text => <Checkbox checked={text} />,
  },
  {
    title: 'Chi',
    dataIndex: 'isOutcome',
    key: 'isOutcomes',
    align: 'center',
    render: text => <Checkbox checked={text} />,
  },
];

export const generalColumns = [
  {
    title: 'STT',
    dataIndex: 'index',
    key: 'index',
    align: 'center',
    width: '10%',
  },
  {
    title: 'Khoản thu',
    dataIndex: 'receivable',
    key: 'receivable',
    align: 'left',
    width: '30%',
  },
  {
    title: 'Số tiền phải nộp',
    dataIndex: 'amount',
    key: 'amount',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
  {
    title: 'Số tiền đã nộp',
    dataIndex: 'paidAmt',
    key: 'paidAmt',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
  {
    title: 'Số tiền hoàn trả',
    dataIndex: 'returnAmt',
    key: 'returnAmt',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
  {
    title: 'Số tiền thiếu/thừa ',
    dataIndex: 'remainAmt',
    key: 'remainAmt',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
];



